<template>
  <div style="background: #f5f5f5;">
    <detail-header></detail-header>
    <detail-info id="info" :c_info="info_c" :Info="Info_Hide" :joins="info_joining"></detail-info>
    <detail-join id="joining" :joining="join_show" :joins="joining"></detail-join>
    <detail-main id="main"></detail-main>
    <detail-content id="content" :look="looks" :post="posting" :look_hide="look_Hide"></detail-content>
    <detail-continue id="continue"></detail-continue>
    <detail-footer :talks="footer_talk" :talking="footer_talks" :talkHide="footer_Hide"></detail-footer>
  </div>
</template>


<script>
  //import '../assets/js/detail.js';
  import detailHeader from '../components/detail/detailHeader';
  import detailInfo from '../components/detail/detailInfo';
  import detailJoin from '../components/detail/detailJoin';
  import detailMain from '../components/detail/detailMain';
  import detailContent from '../components/detail/detailContent';
  import detailContinue from '../components/detail/detailContinue';
  import detailFooter from '../components/detail/detailFooter';
export default {
  components:{
      detailHeader,
      detailInfo,
      detailMain,
      detailJoin,
      detailContent,
      detailContinue,
      detailFooter
  },
  data(){
  return{
      footer_talks:"none",
      Info_Hide:"none",
      join_show:"none",
      posting:"none"
     }
  },
  methods:{
  info_joining:function(data){
        this.join_show="block";
  },
  joining:function(data){
       this.join_show="none";
  },
  looks:function(data){
        this.posting="block";
  },
  look_Hide:function(data){
       this.posting="none";
  },
  footer_talk:function(data){
       this.footer_talks="block";
  },
  footer_Hide:function(data){
       this.footer_talks="none";
  },
  info_c:function(data){
       this.Info_Hide="block";
        }
    },
    mounted:function(){
    (function($){
        $.noConflict();
        // 点下拉箭头出现简介
        $('#brief').toggle(
            function(){
                $('#briefing').css({display:'block'});
            },
            function(){
                $('#briefing').css({display:'none'});
            }
        );
    })(jQuery);

    function $(id){
        return document.getElementById(id);
    }
    $("more").onclick=function(){
        // 右边的更多
        var Left=document.getElementById("look").style.left;
        console.log(Left);
        var inc=0.2;
        var left=Left-inc;
        var mytime=setInterval(function(){
          left--;
          $("look").style.left=left+"rem";
          if(left<-3.2){
            clearInterval(mytime);
          }
        },80);
      };

        var count1 =27;
        var count2 =5;
        var count3 =1314;
        var count4 =11314;
        var count5 =999;
        var count6 =19;
        // 点赞++
        $('numId1').onclick = function(){
            $("numId1").style.color="#e0680a";
            //1、修改数量
            count1++;
            $('spanId1').innerHTML = count1;
    //			console.log($('spanId1').innerHTML );
        };
        $('numId2').onclick = function(){
            $("numId2").style.color="#e0680a";
            //1、修改数量
            count2++;
            $('spanId2').innerHTML = count2;
    //			console.log($('spanId2').innerHTML );
        };
        // 评论里面的点赞
        $('numId3').onclick = function(){
            $("numId3").style.color="#e0680a";
            //1、修改数量
            count3++;
            $('spanId3').innerHTML = count3;
    //			console.log($('spanId3').innerHTML );
        };
        $('numId4').onclick = function(){
            $("numId4").style.color="#e0680a";
            //1、修改数量
            count4++;
            $('spanId4').innerHTML = count4;
    //			console.log($('spanId4').innerHTML );
        };
        $('numId5').onclick = function(){
            $("numId5").style.color="#e0680a";
            //1、修改数量
            count5++;
            $('spanId5').innerHTML = count5;
    //			console.log($('spanId5').innerHTML );
        };
        $('numId6').onclick = function(){
            $("numId6").style.color="#e0680a";
            //1、修改数量
            count6++;
            $('spanId6').innerHTML = count6;
    //			console.log($('spanId6').innerHTML );
        };
        // 点收藏，星星点击时变色
        $("star").onclick=function(){
            $("star").style.color="#fe3e0e";
    //			$("star").style.size=3+"rem";
    //			$("star").style.transform="Scale(1.2)";
        };
        // 分享
        $("join").onclick=function(){
            $("joining").style.display="block";
        };
        $("mask-join").onclick=function(){
            $("joining").style.display="none";
        };
        // 查看更多
        $("view").onclick=function(){
            $("pos").style.display="block";
        };
        $("lost").onclick=function(){
            $("pos").style.display="none";
        };
        //  评论
        $("talk").onclick=function(){
            $("talk-hide").style.display="block";
        };
        $("talkHide").onclick=function(){
            $("talk-hide").style.display="none";
        };
        $("arrow").onclick=function(){
            window.history.back();
        };
    }
}
</script>

<style>
  @import '../assets/css/detail.css';
</style>



