<template>
	<div>
		<my-center-header></my-center-header>
		<my-center-content></my-center-content>
		<my-center-fixed></my-center-fixed>
		<my-center-footer></my-center-footer>
	</div>
</template>

<script>
	
	
//	import '../assets/js/MyCenter.js';
	
	import MyCenterHeader from '../components/MyCenter/MyCenterHeader';
	import MyCenterContent from '../components/MyCenter/MyCenterContent';
	import MyCenterFixed from '../components/MyCenter/MyCenterFixed';
	import MyCenterFooter from '../components/MyCenter/MyCenterFooter';
	

	export default {
   		components:{
   			MyCenterHeader,
        	MyCenterContent,
        	MyCenterFixed,
       		MyCenterFooter
    	},   
		data(){
			return{
				//css原本样式里面属性值
//				fixeds:"none"
			}
			
		},
		mounted:function(){
			/*// 滚动条滑动，定位出现
			
			window.onscroll = function(){
//				let dixed = document.getElementById('dixed');
				let scrollT = document.documentElement.scrollTop || document.body.scrollTop;
				if(scrollT>=2){
					$("#dixed").css({display:"block"});
				}else{
					$("#dixed").css({display:"none"});
				}
			}*/
			(function($){
				$.noConflict();
				// 滚动条滑动，定位出现
				var dixed = document.getElementById('dixed');
				window.onscroll = function(){
					var scrollT = document.documentElement.scrollTop || document.	body.scrollTop;
					if(scrollT>=2){
						dixed.style.display = "block";
					}else{
						dixed.style.display = "none";
					}
				}
				$("#contentBtn").click(function(){
					window.location.href="/#/logout";
				})
				$("#content1-Btn").click(function(){
					window.location.href="/#/openMembership";
				})
				$("#logoid").click(function(){
					window.location.href="/#/login";
				})
			})(jQuery)
		}
  	}

</script>

<style>
	@import '../assets/css/MyCenter.css';
</style>


