import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import vip from '../pages/vip.vue'
import index from '../pages/index.vue'


import login from '../pages/login.vue'
import logout from '../pages/logout.vue'
import mycenter from '../pages/mycenter.vue'
import openmembership from '../pages/openmembership.vue'
import concern from '../pages/concern.vue'
import alive from '../pages/alive.vue'

import reg from '@/pages/reg'
import detail from '@/pages/detail'
import live from '@/pages/live'
import password from '@/pages/password'
import search from '@/pages/search'





Vue.use(Router)

export default new Router({
  routes: [
   /*{
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },*/

  	{
      path: '/',
      name: 'index',
      component: index
    },

    {
      path: '/vip',
      name: 'vip',
      component: vip
    },

    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/logout',
      name: 'logout',
      component: logout
    },
    {
      path: '/mycenter',
      name: 'mycenter',
      component: mycenter
    },
    {
      path: '/openmembership',
      name: 'openmembership',
      component: openmembership
    },
    {
      path: '/concern',
      name: 'concern',
      component: concern
    },
    {
      path: '/alive',
      name: 'alive',
      component: alive
    },
    {
      path: '/reg',
      name: 'reg',
      component: reg
    },
    {
      path: '/detail',
      name: 'detail',
      component: detail
    },
    {
      path: '/live',
      name: 'live',
      component: live
    },
    {
      path: '/password',
      name: 'password',
      component: password
    },
    {
      path: '/search',
      name: 'search',
      component: search
    }



  ]
})



