<template>
    <div style="background: #eeeeee;">

          <vip-header></vip-header>

          <vip-nav></vip-nav>
         
          <vip-main></vip-main>
  
          <vip-footer></vip-footer>
          
    </div>

   
</template>

<script type="text/ecmascript-6">
    import VipHeader from "../components/vip/VipHeader.vue";
    import VipNav from "../components/vip/VipNav.vue";
    import VipMain from "../components/vip/VipMain.vue";
    import VipFooter from "../components/vip/VipFooter.vue";

     export default {
      components: {
        VipHeader,
        VipNav,
        VipMain,
        VipFooter
      },
       mounted:function(){
            var mySwiper = new Swiper ('.swiper-container', {
              autoplay : 5000,
              autoplayDisableOnInteraction :false,
              direction: 'horizontal',
              loop: true,
                        
              // 如果需要分页器
               pagination: '.swiper-pagination',
            })

             console.log(mySwiper);
       }
    }

</script> 
<style> 
    @import'../assets/css/vip.css';

</style>



