<template>
  <div style="background:#fff;height:100%;">
    <reg-header></reg-header>
    <reg-main id="main" :maskings="masking_reg"></reg-main>
    <reg-mask id="mask" :mask="maskings" :perfects="perfecting"></reg-mask>
  </div>
</template>


<script>
  // import '../assets/js/reg.js';
  import regHeader from '../components/reg/regHeader';
  import regMain from '../components/reg/regMain';
  import regMask from '../components/reg/regMask';
export default {
  components:{
      regHeader,
      regMain,
      regMask
  },
  data(){
  return{
      maskings:"none"
     }
  },
  methods:{
  masking_reg:function(data){
          this.maskings="block";
          },
  perfecting:function(data){
      this.maskings="none";
      }

  },
  mounted:function(){
  //
  function $(id){
    return document.getElementById(id);
  }
  $('code').onclick=function(){
     code();
     document.getElementById("code").innerHTML=code();
  };
  function code(){
      var num="";
      var iIndex=0;//随机数[0,9]
     for(var i=0;i<4;i++){
          iIndex=parseInt(Math.random()*10);
          num+=iIndex;   //把随机数子拼接到一起
     }
      return num;
  }
  // 点验证提示击时候的
   //1、定义规则：
      //手机号码的规则，1开头，第二位是（3,4,5,7,8,9），一共十一位纯数字
      var regObj1 = /^1[345789]\d{9}$/i;
      $("userId").onblur = function(){
          //var regObj1 = /^1[345789]\d{9}$/i;
          if(regObj1.test(this.value)){
              $("inter").innerHTML = "";
              $("inter").style.display="none";
              console.log(1)
          }else{
              $("inter").innerHTML = "请输入正确的手机号!";
              $("inter").style.display="block";
              setInterval(function(){
                  $("inter").style.display="none";
              },2500);
              //window.location.href="reg.html";
          }
      };

      //1、定义规则：
      //验证码的规则，1开头，第二位是（3,4,5,7,8,9），一共四位纯数字
      $("codeId").onkeydown=function(){
          $("step").style.background="#ff6600";
      };
      var regObj2 = /^\d{4}$/i;
      $("codeId").onblur = function(){
  			//var regObj2 = /^\d{4}$/i;
          if(regObj2.test(this.value)){
              $("inter").innerHTML = "";
              $("inter").style.display="none";
              console.log(1)
          }else{
              $("inter").innerHTML = "请输入正确的验证码";
              $("inter").style.display="block";
              setInterval(function(){
                  $("inter").style.display="none";
              },2500);
          }
      };
    //点击下一步进行判断
          $("step").onclick=function(){

              //验证用户名格式是否正确
               if(regObj1.test($("userId").value)){
                  $("inter").innerHTML = "";
                  $("inter").style.display="none";

                  //如果用户名输入正确进行下面的验证
                   if(regObj2.test($("codeId").value)){
                      $("inter").innerHTML = "";
                      $("inter").style.display="none";

                       //验证都正确，保存cookie，跳到下一步
                       var userName=$("userId").value;
                      var userId={
                          "userName":userName
                      }
                       window.location.href="/#/password";

                  }else{
                      $("inter").innerHTML = "请输入正确的验证码";
                      $("inter").style.display="block";
                      setInterval(function(){
                          $("inter").style.display="none";
                      },2500);
                      return;
                  }

              }else{
                  $("inter").innerHTML = "请输入正确的手机号!";
                  $("inter").style.display="block";
                  setInterval(function(){
                      $("inter").style.display="none";
                  },2500);
                   return;
              }
          };

      $("back").onclick=function(){
          window.history.back();
      };


  }
}
</script>

<style>
  @import '../assets/css/reg.css';
</style>



