<template>
	<div style="background: #4B3527;height:100%; display: flex;flex-direction: column;">
		<alive-header></alive-header>
        <alive-main></alive-main>
        <alive-footer></alive-footer>
        <alive-present></alive-present>
        <alive-inputdanmu></alive-inputdanmu>
	</div>
</template>

<script>

    import AliveHeader from "../components/alive/AliveHeader.vue";
    import AliveMain from "../components/alive/AliveMain.vue";
    import AliveFooter from "../components/alive/AliveFooter.vue";
    import AlivePresent from "../components/alive/AlivePresent.vue";
	import AliveInputdanmu from "../components/alive/AliveInputdanmu.vue";

	export default {
		components: {
        	AliveHeader,
            AliveMain,
            AliveFooter,
            AlivePresent,
            AliveInputdanmu
      	},
        mounted : function(){
            (function($){
                $.noConflict();
                $('.parent').click(function(){
                    $('.present').removeClass('dn');
                    var mySwiper = new Swiper (
                                        '.swiper-container', {
                                            speed:1000,
                                            loop:false,
                                            autoplayDisableOnInteraction :false,
                                            effect : 'effect',
                                            paginationClickable: true,
                                            // 如果需要分页器
                                            pagination: '.swiper-pagination',
                                            }
                                    )
                });
                
                $('.topbox').click(function(){
                    $(this).parent().addClass('dn');
                });

                $('.danmu').click(function(){
                    if($(this).attr('src')=='imgs/danmu.png'){
                        $(this).attr('src','imgs/danmu1.png');
                        $('.barrage').addClass('dn');
                    }else{
                        $(this).attr('src','imgs/danmu.png');
                        $('.barrage').removeClass('dn');
                    }
                });

                $('.indanmu').click(function(){
                    $('.inputdanmu').removeClass('dn');
                    $(".content input").focus();
                })
                $('.space').click(function(){
                    $(this).parent().addClass('dn');
                });
                
                $('.send').click(function(){
                    if($('.content input').val()!=''){
                        $('.space').parent().addClass('dn');
                        let str = document.createElement('li');
                        str.innerHTML = '<img src="../assets/imgs/2018-01-18_110252.jpg" alt="">' +
                                '<span class="username" id="userName">好多糖65pny</span>' +
                                '<span class="username">:</span>' +
                                '<span class="say">'+$('.content input').val()+'</span>'
                        $('.content input').val('');
                        console.log(str);                      
                        $('.barrage ul')[0].appendChild(str);
                    };
                });
            })(jQuery)

        }
    }        
</script>
<style> 
    @import'../assets/css/alive.css';
</style>



