<template>
<div id="banner">
  <div class="swiper-container">
    <div class="swiper-wrapper">
      <div class="swiper-slide">
        <img src="../../assets/imgs/live-banner.jpg"/>
      </div>
      <div class="swiper-slide">
        <img src="../../assets/imgs/live-banner1.jpg"/>
      </div>
      <div class="swiper-slide">
        <img src="../../assets/imgs/live-banner2.jpg"/>
      </div>
      <div class="swiper-slide">
        <img src="../../assets/imgs/live-banner3.jpg"/>
      </div>
    </div>
    <!-----------分页器------------------------>
    <div class="swiper-pagination"></div>
  </div>
    <!--<div id="img"><img src="../../assets/imgs/live-banner.jpg" /></div>-->
    <p>【2018-2019湖南卫视跨年演唱会】全程回顾</p>
</div>
</template>

<script>
export default {

   methods:{

      },
 mounted:function(){
      function $(id){
        return document.getElementById(id);
      }
      var mySwiper= new Swiper ('.swiper-container', {
                autoplay : 4000,
                autoplayDisableOnInteraction:false,
                direction: 'horizontal',
                loop: true,

                // 如果需要分页器
                pagination: '.swiper-pagination',
              }) ;
      }
}

</script>

<style>
</style>



