<template>
	<div>
		<open-membership-box></open-membership-box>
		<open-membership-matte></open-membership-matte>
	</div>
</template>

<script>
	
//	import '../assets/js/OpenMembership.js';
	
	import OpenMembershipBox from '../components/openMembership/OpenMembershipBox';
	import OpenMembershipMatte from '../components/openMembership/OpenMembershipMatte';
	
//	import '../assets/js/bufferMove.js';
	
	
	
	export default {
   		components:{
   			OpenMembershipBox,
   			OpenMembershipMatte
    	},   
		data(){
			return{
				//css原本样式里面属性值
				
			}
			
		},
		mounted:function(){
			function $(id) {
				return document.getElementById(id);
			}
			function bufferMove(obj, target, fn, ratio = 8) {
				
				clearInterval(obj.timer);
				obj.timer = setInterval(function() {
					//用来判断一个运动是否执行完根据这个值来清除定时器
					var bBtn = true;
					for(var sAttr in target) {
						if(sAttr === 'opacity') {
							var iCur = getStyle(obj, 'opacity') * 100;
						} else {
							var iCur = parseInt(getStyle(obj, sAttr));
						}
						var speed = (target[sAttr] - iCur) / ratio;
		
						speed = speed > 0 ? Math.ceil(speed) : Math.floor(speed);
						var next = iCur + speed;
		
						if(sAttr === 'opacity') {
							obj.style.opacity = next / 100;
							//兼容IE
							obj.style.filter = 'alpha(opacity:' + next + ')';
						} else {
							obj.style[sAttr] = next + 'px';
						}
		
						if(target[sAttr] !== next) {
							bBtn = false;
						}
					}
		
					if(bBtn === true) {
						clearInterval(obj.timer);
						if(fn) {
							fn();
						}
					}
				}, 50)
			}
		
			function getStyle(obj, sAttr) {
				if(obj.currentStyle) {
					return obj.currentStyle[sAttr];
				} else {
					return getComputedStyle(obj, false)[sAttr];
				}
			}
		
			//点击切换对号勾选
			var payment2 = document.getElementsByClassName("payment2-1");
			var payment3 = document.getElementsByClassName("payment2-2");
		//	 console.log(payment2[0])
			payment2[0].onclick = function() {
				payment3[0].style.display = "block";
				payment2[0].style.display = "none";
				payment3[1].style.display = "none";
				payment3[2].style.display = "none";
			}
			payment3[0].onclick = function() {
				payment3[0].style.display = "none";
				payment2[0].style.display = "block";
				payment3[1].style.display = "none";
				payment2[1].style.display = "block";
				payment3[2].style.display = "none";
				payment2[2].style.display = "block";
				
			}
		
			payment2[1].onclick = function() {
				payment3[1].style.display = "block";
				payment2[1].style.display = "none";
				payment2[0].style.display = "none";
				payment3[0].style.display = "block";
				payment3[2].style.display = "none";
				payment2[2].style.display = "block";
			}
			payment3[1].onclick = function() {
				payment3[1].style.display = "none";
				payment2[1].style.display = "block";
			}
		
			payment2[2].onclick = function() {
				payment3[2].style.display = "block";
				payment2[2].style.display = "none";
				payment3[1].style.display = "none";
				payment2[1].style.display = "block";
				payment2[0].style.display = "none";
				payment3[0].style.display = "block";
			}
			payment3[2].onclick = function() {
				payment3[2].style.display = "none";
				payment2[2].style.display = "block";
			}
		
			//点击切换对号勾选
			var movement3 = $("movement3-1");
			var movement2 = $("movement3-2");
			movement2.onclick = function() {
				$("omovement2").style.display = "none";
				$("movement2").style.display = "block";
				movement3.style.display = "block";
				movement2.style.display = "none";
		
				notarize0.innerHTML = amount2d[0].innerHTML;
		
				amount[0].className = "amount0";
				amount2s[0].children[0].className = "amount2-2";
				money[0].children[1].className = "amount2-1";
		
				amount[1].className = "";
				amount2s[1].children[0].className = "";
				money[1].children[1].className = "";
		
				amount[2].className = "";
				amount2s[2].children[0].className = "";
				money[2].children[1].className = "";
			}
			movement3.onclick = function() {
				$("omovement2").style.display = "block";
				$("movement2").style.display = "none";
				movement2.style.display = "block";
				movement3.style.display = "none";
		
				notarize0.innerHTML = oamount2d[0].innerHTML;
		
				oamount[0].className = "oamount0";
				oamount2s[0].children[0].className = "oamount2-2";
				omoney[0].children[1].className = "oamount2-1";
		
				oamount[1].className = "";
				oamount2s[1].children[0].className = "";
				omoney[1].children[1].className = "";
		
				oamount[2].className = "";
				oamount2s[2].children[0].className = "";
				omoney[2].children[1].className = "";
		
			}
		
			//点击切换对号勾选
			var Alipay1 = document.getElementsByClassName("Alipay1-1");
			var Alipay2 = document.getElementsByClassName("Alipay1-2");
			// console.log(payment2)
			Alipay1[0].onclick = function() {
				Alipay2[0].style.display = "block";
				Alipay1[0].style.display = "none";
			}
			Alipay2[0].onclick = function() {
				Alipay2[0].style.display = "none";
				Alipay1[0].style.display = "block";
			}
		
			// 点击ul下面li时，边框变色，文字变大，改变背景
			var amount = $("amount").children;
			var money = document.getElementsByClassName("money");
			var amount2s = document.getElementsByClassName("amount2");
			var amount2d = document.getElementsByClassName("amount2-1");
			var notarize0 = $("notarize0");
			let tabs = document.getElementsByClassName("tab1");
			// console.log(amount2)
			for(let i = 0; i < amount.length; i++) {
				// console.log(amount[i])
				amount[i].onclick = function() {
					if(i == 0) {
						for(let x = 0; x < tabs.length; x++) {
							tabs[x].style.display = "none";
						}
						tabs[0].style.display = "block";
					} else if(i == 1 || i == 2) {
						for(let y = 0; y < tabs.length; y++) {
							tabs[y].style.display = "none";
						}
						tabs[1].style.display = "block";
					}
					for(let j = 0; j < amount.length; j++) {
						amount[j].className = "";
						amount2s[j].children[0].className = "";
						money[j].children[1].className = "";
		
					}
					amount[i].className = "amount0";
					amount2s[i].children[0].className = "amount2-2";
					money[i].children[1].className = "amount2-1";
		
					// 点击改变价钱
					notarize0.innerHTML = money[i].children[1].innerHTML;
					// console.log()	
				}
			}
		
			// 点击ul下面li时，边框变色，文字变大，改变背景
			var oamount = $("oamount").children;
			var omoney = document.getElementsByClassName("omoney");
			var oamount2s = document.getElementsByClassName("oamount2");
			var oamount2d = document.getElementsByClassName("oamount2-1");
			var notarize0 = $("notarize0");
			// let otabs=document.getElementsByClassName("otab1");
			// console.log(amount2)
			for(let i = 0; i < oamount.length; i++) {
				// console.log(amount[i])
				oamount[i].onclick = function() {
					if(i == 0) {
						for(let x = 0; x < tabs.length; x++) {
							tabs[x].style.display = "none";
						}
						tabs[0].style.display = "block";
					} else if(i == 1 || i == 2) {
						for(let y = 0; y < tabs.length; y++) {
							tabs[y].style.display = "none";
						}
						tabs[1].style.display = "block";
					}
					for(let j = 0; j < oamount.length; j++) {
						oamount[j].className = "";
						oamount2s[j].children[0].className = "";
						omoney[j].children[1].className = "";
					}
					oamount[i].className = "oamount0";
					oamount2s[i].children[0].className = "oamount2-2";
					omoney[i].children[1].className = "oamount2-1";
		
					// 点击改变价钱
					notarize0.innerHTML = omoney[i].children[1].innerHTML;
					// console.log()	
				}
			}
		
			// 点击header2的时候，刷新页面出现
			var header2 = $("header2");
			var flush = $("flush");
			var matte = $("matte");
			header2.onclick = function() {
				flush.style.display = "block";
				bufferMove(flush, {
					bottom: 0
				});
				matte.style.display = "block";
			}
		
			matte.onclick = function() {
				bufferMove(flush, {
					bottom: -400
				});
				matte.style.display = "none";
				flush.style.display = "none";
			}
			
			var login3=document.getElementById("login3-1");
			login3.onclick=function(){
				window.location.href="/#/login";
			}
			
			var btnH=document.getElementById("btnH");
			btnH.onclick=function(){
				window.location.href="/#/MyCenter";
//				this.$router.push('/MyCenter');
			}
		}
  	}
</script>

<style>
	@import '../assets/css/openMembership.css';
</style>


