<template>
	<div>
		<login-box1 :ShortMessage="ShortMessage0" :_box1="box0" :Accounts="Accountss"></login-box1>
		<login-box2 :_box2="boxstyle2" :ArrowClick="ArrowClick1" :region="regions"></login-box2>
		<login-matte :Mattes="Mattess" :finishs="finishss"></login-matte>
		
	</div>
</template>

<script>
	
//	import '../assets/js/login.js';
	
	import LoginBox1 from '../components/login/LoginBox1';
	import LoginBox2 from '../components/login/LoginBox2';
	import LoginMatte from '../components/login/LoginMatte';
	
	export default {
   		components:{
        	LoginBox1,
        	LoginBox2,
       		LoginMatte
    	},   
		data(){
			return{
				//css原本样式里面属性值
				box0:"block",
				boxstyle2:"none",
				Mattess:"none",
			}	
		},
		methods:{
			ShortMessage0:function(data){
				this.box0="none";
				this.boxstyle2="block";
			},
			ArrowClick1:function(data){
				this.box0="block";
				this.boxstyle2="none";
			},
			Accountss:function(data){
				this.Mattess="block";
			},
			finishss:function(data){
				this.Mattess="none";
			},
			regions:function(data){
				this.Mattess="block";
			}
		},
		
		mounted:function(){

			//鼠标点击文字改变颜色和下划线
			var AccountLoginbtn = document.getElementById('AccountLoginbtn');
			var box1 = document.getElementById('box1');
			var box2 = document.getElementById('box2');
			var arrow = document.getElementById('arrow');
			AccountLoginbtn.onclick=function(){
				box1.style.display = "none";
				box2.style.display = "block";
			}
			arrow.onclick = function(){
				box1.style.display = "block";
				box2.style.display = "none";
			}
		
			// 点击finish（完成）时，让蒙版出现
			var finish = document.getElementById("finish");
			var Matte = document.getElementById("Matte");
			var cnBtn = document.getElementById("cnBtn");
			var Account11 = document.getElementById("Account1-1");
			var Account12 = document.getElementById("Account11-1");
		
			Account11.onclick = function(){
				Matte.style.display = "block";
			}
		
			finish.onclick = function(){
				Matte.style.display = "none";
			}
		
			Account12.onclick = function(){
				Matte.style.display = "block";
			}
		
			// 手机正则验证
			var mobliePhone = document.getElementById('mobliePhone');
			var mobliePhoneMsg = document.getElementById('mobliePhoneMsg');
		
			var regObj = /^1[345789]\d{9}$/i;
			mobliePhone.onblur = function() {
				//1、定义规则：
				//手机号码的规则，1开头，第二位是（3,4,5,7,8,9），一共十一位纯数字
		
				if(regObj.test(this.value)) {
					mobliePhoneMsg.style.display = "none";
				} else {
					mobliePhoneMsg.style.display = "block";
					//定时器
					var myTimer = null;
					//定时器
					clearInterval(myTimer);
					myTimer = setInterval(function() {
						mobliePhoneMsg.style.display = "none";
					}, 2000);
				}
			}
		
			// 判断如果手机号和密码都输入后，登陆背景色改变
			var AccountLogin12 = document.getElementById("AccountLogin-2");
			var Account2 = document.getElementById("Account2-1");
			var AccountLogin21 = document.getElementById("AccountLogin-2-1");
			Account2.oninput = function() {
				// console.log(1)
				if(mobliePhone.value !== "" && Account2.value !== "") {
					AccountLogin12.style.display = "none";
					AccountLogin21.style.display = "block";
				} else {
					AccountLogin12.style.display = "block";
					AccountLogin21.style.display = "none";
				}
			}
		
			mobliePhone.oninput = function() {
				// console.log(1)
				if(mobliePhone.value !== "" && Account2.value !== "") {
					AccountLogin12.style.display = "none";
					AccountLogin21.style.display = "block";
				} else {
					AccountLogin12.style.display = "block";
					AccountLogin21.style.display = "none";
				}
			}
		
			var mobliePhone1 = document.getElementById('mobliePhone1');
			var mobliePhoneMsg1 = document.getElementById('mobliePhoneMsg1');
		
			mobliePhone1.onblur = function() {
				//1、定义规则：
				//手机号码的规则，1开头，第二位是（3,4,5,7,8,9），一共十一位纯数字
				var regObj1 = /^1[345789]\d{9}$/i;
				if(regObj1.test(this.value)) {
					mobliePhoneMsg1.style.display = "none";
				} else {
					mobliePhoneMsg1.style.display = "block";
					//定时器
					var myTimer1 = null;
					//定时器
					clearInterval(myTimer1);
					myTimer1 = setInterval(function() {
						mobliePhoneMsg1.style.display = "none";
					}, 2000);
					// console.log(1)
				}
			}
		
			// 判断如果手机号和密码都输入后，登陆背景色改变
			var AccountLogin121 = document.getElementById("AccountLogin1-2");
			var Account1 = document.getElementById("Account21-1");
			var AccountLogin211 = document.getElementById("AccountLogin2-2");
			Account1.oninput = function() {
				// console.log(1)
				if(mobliePhone1.value !== "" && Account1.value !== "") {
					AccountLogin121.style.display = "none";
					AccountLogin211.style.display = "block";
				} else {
					AccountLogin121.style.display = "block";
					AccountLogin211.style.display = "none";
				}
			}
		
			mobliePhone1.oninput = function() {
				// console.log(1)
				if(mobliePhone1.value !== "" && Account1.value !== "") {
					AccountLogin121.style.display = "none";
					AccountLogin211.style.display = "block";
				} else {
					AccountLogin121.style.display = "block";
					AccountLogin211.style.display = "none";
				}
			}
		
			var back = document.getElementById("back");
			back.onclick = function() {
//					console.log(1)
				window.history.back();
			}
			
			document.getElementById("header2").onclick=function(){
//			    this.$router.push('/reg');
			    window.location.href="/#/reg";
			}
			
		}
  	}

</script>

<style>
	@import '../assets/css/login.css';
	
</style>


