<template>
  <div style="background: #fff;">
    <search-header></search-header>
    <search-list></search-list>
    <search-hot></search-hot>
    <search-main></search-main>
    <search-bottom></search-bottom>
  </div>
</template>


<script>
  // import '../assets/js/search.js';
  import searchHeader from '../components/search/searchHeader';
  import searchList from '../components/search/searchList';
  import searchHot from '../components/search/searchHot';
  import searchMain from '../components/search/searchMain';
  import searchBottom from '../components/search/searchBottom';
export default {
  components:{
      searchHeader,
      searchList,
      searchHot,
      searchMain,
      searchBottom
  },
  data(){
  return{

     }
  },
  methods:{

    },
    mounted:function(){
      function $(id){
      	return document.getElementById(id);
      }
      var lis=$("menue").children;
      let conlis=$("change-content").children;

      for(let i=0;i<lis.length;i++){
      		lis[i].style.left=0.9*(i%4)+0.5+"rem";
      }
      for(let i=0;i<lis.length;i++){
      	lis[i].onclick=function(){
      		$("moveColor").style.left=0.9*(i%4)+0.5+0.23+"rem";
      		for(let j=0;j<lis.length;j++){
      			lis[j].className="";
      		}
      		lis[i].className="showcolor";

      		for(let j=0;j<conlis.length;j++){
      			conlis[j].className="";
      		}
      		conlis[i].className="showli";
      	}
      }



    function callback(d){
        for(let i=0;i<d.data.length;i++){
          var li=document.createElement("li");
          var a=document.createElement("a");
          a.href="detail.html"
          a.innerHTML=d.data[i].showTitle;
          li.appendChild(a);
          list.appendChild(li);
        }
      }
      window.callback = callback;


        $("searchBox").oninput=function(){
          console.log(1);
        	$("list").style.display="block";
        	/*list.innerHTML="";*/
        	var val=this.value;
        	if(val!=""){
            console.log(2);
        		var script=document.createElement("script");
        		script.src="https://mobileso.bz.mgtv.com/pc/suggest/v2?callback=callback&_support=10000000&q="+val+"&_=1515492";
        		document.body.appendChild(script);
        	}
        }
        $("searchBox").onkeyup=function(){
           if($("searchBox").value.length==0){
              $("list").style.display="none";
           }
        };

        $("closeSearch").onclick=function(){
        	 window.history.back();
        }

    }
}
</script>

<style>
  @import '../assets/css/search.css';
</style>



