<script type="text/ecmascript-6">

    export default {

        mounted:function(){
            function $(id){
                return document.getElementById(id);
            }
            var mySwiper = new Swiper ('.swiper-container', {
              autoplay : 5000,
              autoplayDisableOnInteraction :false,
              direction: 'horizontal',
              loop: true,
                        
              // 如果需要分页器
               pagination: '.swiper-pagination',
            })

             

             //头条换一换效果
            showTopline();
            function showTopline(){
                let uls=document.getElementsByClassName("exchange-con");
                let count=0;
                $("top-exchange").onclick=function(){
                    count++;
                    if(count>2){
                        count=0;
                    }
                    for(let i=0;i<uls.length;i++){
                        uls[i].style.display="none";
                    }
                    uls[count].style.display="block";
                }
            }

            //今日热门换一换效果
            showHotline();
            function showHotline(){
                let uls=document.getElementsByClassName("hot-exchange-con");
                let count=0;
                $("hot-exchange").onclick=function(){
                    count++;
                    if(count>1){
                        count=0;
                    }
                    for(let i=0;i<uls.length;i++){
                        uls[i].style.display="none";
                    }
                    uls[count].style.display="block";
                }
            }
       }
    }
</script>




