<template>
	<div>
		<concern-header></concern-header>
		<concern-center></concern-center>
		<concern-footer></concern-footer>
	</div>
</template>

<script>
	import ConcernHeader from '../components/concern/ConcernHeader';
	import ConcernCenter from '../components/concern/ConcernCenter';
	import ConcernFooter from '../components/concern/ConcernFooter';

	export default {
		components: {
			ConcernHeader,
			ConcernCenter,
			ConcernFooter
		},
		mounted: function() {

			// 点击时变化颜色
			var Chotspot3 = document.getElementsByClassName('Chotspot3-2');
			var cBtn = document.getElementsByClassName('cBtn');
			var cMar = document.getElementsByClassName('cMar');
			// console.log(Chotspot3)
			var num = cMar[0].innerHTML;

			Chotspot3[0].onclick = function() {
				num++;

				if(cBtn[0].style.color == "rgb(255, 95, 1)") {
					return;
				}
				cBtn[0].style.color = "#ff5f01";
				cMar[0].innerHTML = num;
			}

			// 点击时变化颜色
			var aChotspot3 = document.getElementsByClassName('aChotspot31-2');
			var acBtn = document.getElementsByClassName('acBtn');
			var acMar = document.getElementsByClassName('acMar');
			// console.log(Chotspot3)
			var num1 = acMar[0].innerHTML;

			aChotspot3[0].onclick = function() {
				num1++;

				if(acBtn[0].style.color == "rgb(255, 95, 1)") {
					return;
				}
				acBtn[0].style.color = "#ff5f01";
				acMar[0].innerHTML = num1;
			}

			// 点击时变化颜色
			var bChotspot3 = document.getElementsByClassName('bChotspot31-2');
			var bcBtn = document.getElementsByClassName('bcBtn');
			var bcMar = document.getElementsByClassName('bcMar');
			// console.log(Chotspot3)
			var num2 = bcMar[0].innerHTML;

			bChotspot3[0].onclick = function() {
				num2++;

				if(bcBtn[0].style.color == "rgb(255, 95, 1)") {
					return;
				}
				bcBtn[0].style.color = "#ff5f01";
				bcMar[0].innerHTML = num2;
			}

			// 点击时变化颜色
			var cChotspot3 = document.getElementsByClassName('cChotspot31-2');
			var ccBtn = document.getElementsByClassName('ccBtn');
			var ccMar = document.getElementsByClassName('ccMar');
			// console.log(Chotspot3)
			var num3 = ccMar[0].innerHTML;

			cChotspot3[0].onclick = function() {
				num3++;

				if(ccBtn[0].style.color == "rgb(255, 95, 1)") {
					return;
				}
				ccBtn[0].style.color = "#ff5f01";
				ccMar[0].innerHTML = num3;
			}

			// 点击时变化颜色
			var dChotspot3 = document.getElementsByClassName('dChotspot31-2');
			var dcBtn = document.getElementsByClassName('dcBtn');
			var dcMar = document.getElementsByClassName('dcMar');
			// console.log(Chotspot3)
			var num4 = dcMar[0].innerHTML;

			dChotspot3[0].onclick = function() {
				num4++;

				if(dcBtn[0].style.color == "rgb(255, 95, 1)") {
					return;
				}
				dcBtn[0].style.color = "#ff5f01";
				dcMar[0].innerHTML = num4;
			}
			
			

		}
	}
</script>

<style>
	@import '../assets/css/concern.css';
</style>


