<template>
  <div style="background: #f5f5f5;">
    <live-header id="hall"></live-header>
    <live-banner id="banner"></live-banner>
    <live-bigEvent id="bigEvent"></live-bigEvent>
    <live-tv id="tv"></live-tv>
    <live-playback id="playback"></live-playback>
    <live-attention id="attention"></live-attention>
    <live-footer class="footer"></live-footer>
  </div>
</template>


<script>
   import '../assets/js/swiper-3.4.2.min.js'

  import liveHeader from '../components/live/liveHeader';
  import liveBanner from '../components/live/liveBanner';
  import liveBigEvent from '../components/live/liveBigEvent';
  import liveTv from '../components/live/liveTv';
  import livePlayback from '../components/live/livePlayback';
  import liveAttention from '../components/live/liveAttention';
  import liveFooter from '../components/live/liveFooter';
export default {
  components:{
      liveHeader,
      liveBanner,
      liveBigEvent,
      liveTv,
      livePlayback,
      liveAttention,
      liveFooter
  },
  data(){
  return{

     }
  },
  methods:{

    },
    mounted:function(){

    function $(id){
      return document.getElementById(id);
    }
    // tab切换
    $("tab2").onclick = function () {
        // 直播关注
        console.log(1);
        $("attention").style.display = "block";
        $("tab1").style.color = "#868686";
        $("tab1").style.borderColor = "#222";
        $("tab2").style.color = "#fff";
        $("tab2").style.borderColor = "#db6c2c";
      };
      $("tab1").onclick = function () {
        // 直播关注

        $("attention").style.display = "none";
        $("tab1").style.color = "#fff";
        $("tab1").style.borderColor = "#db6c2c";
        $("tab2").style.color = "#868686";
        $("tab2").style.borderColor = "#222";
      };

      $("chan").onclick = function () {
        $("channel").style.display = "block";
      };
      $("goBack").onclick = function () {
        $("channel").style.display = "none";
      };
    }
}
</script>

<style>
  @import '../assets/css/live.css';
</style>



