<template>
<div>
	
	<!---------------------------离线缓存框---------------------------->
	<div id="cache">
		<h5>
			<span id="closesildCache">&#xe625;</span>
			离线缓存
		</h5>
		<b>暂无缓存视屏</b>
	</div>

	<!---------------------------暂无播放记录---------------------------->
	<div id="history">
		<h5>
			<span id="closesildHistory">&#xe625;</span>
			播放记录
		</h5>
		<b>暂无播放记录</b>
	</div>

	<header id="index-header">
		<h1>
			
		</h1>
		<div>
			<span>&#xe6a4;</span>
			<input type="text" id="search" @click="showSearch" placeholder="胡一天">
		</div>
		
		<ul>
			<li id="cacheBtn" @click="showCache">
				<a href="javascript:;">&#xe6a5;</a>
			</li>
			<li id="historyBtn" @click="showHistory">
				<a href="javascript:;">&#xe735;</a>
			</li>
		</ul>
	</header>
</div>
	
</template>

<script>

	function bufferMove(obj, target, fn, ratio = 8) {
  clearInterval(obj.timer);
  obj.timer = setInterval(function() {
    //用来判断一个运动是否执行完根据这个值来清除定时器
    var bBtn = true;
    for (var sAttr in target) {
      if (sAttr === 'opacity') {
        var iCur = getStyle(obj, 'opacity') * 100;
      } else {
        var iCur = parseInt(getStyle(obj, sAttr));
      }
      var speed = (target[sAttr] - iCur) / ratio;

      speed = speed > 0 ? Math.ceil(speed) : Math.floor(speed);
      var next = iCur + speed;

      if (sAttr === 'opacity') {
        obj.style.opacity = next / 100;
        //兼容IE
        obj.style.filter = 'alpha(opacity:' + next + ')';
      } else {
        obj.style[sAttr] = next + 'px';
      }

      if (target[sAttr] !== next) {
        bBtn = false;
      }
    }

    if (bBtn === true) {
      clearInterval(obj.timer);
      if (fn) {
        fn();
      }
    }
  }, 50)
}

function getStyle(obj, sAttr) {
  if (obj.currentStyle) {
    return obj.currentStyle[sAttr];
  } else {
    return getComputedStyle(obj, false)[sAttr];
  }
}

function $(id){
    return document.getElementById(id);
}  

	export default {
	   	methods:{
      		/*godetail:function(productId){
        		this.$router.push("/detail?productId="+productId);
      		}*/

      		showSearch:function(){
              this.$router.push("/search");
            	/*window.location.hash="/vip";*/
      		},
      		showCache:function(){
      			 
      			$("cache").style.display="block";
				    bufferMove($("cache"),{left:0})
      		},
      		showHistory:function(){
      			
      			$("history").style.display="block";
				    bufferMove($("history"),{left:0})
      		}
    	},

    	mounted:function(){
      		
      			$("closesildCache").onclick=function(){
					     bufferMove($("cache"),{left:760},function(){
					     $("cache").style.display="none";
					     })
				}

				$("closesildHistory").onclick=function(){
					   bufferMove($("history"),{left:760},function(){
						    $("history").style.display="none";
					   })
				}
      }
	}
</script>



