<template>
  <div>
    <pass-header></pass-header>
    <pass-main id="pass-main"></pass-main>
  </div>
</template>


<script>
  // import '../assets/js/password.js';
  import passHeader from '../components/password/passHeader';
  import passMain from '../components/password/passMain';
export default {
  components:{
      passHeader,
      passMain
  },
  data(){
  return{

     }
  },
  methods:{

    },
    mounted:function(){
    function $(id){
      return document.getElementById(id);
    }
    //1、密码定义规则：
      //6位 数字 大小写字母
      $("passId").onkeydown=function(){
        $("success").style.background="#ff6600";
      }
      var regObj = /[a-zA-Z0-9]{6}$/;
      $("passId").onblur = function() {

    //
        if(regObj.test(this.value)){
          $("error").innerHTML = "";
          $("error").style.display="none";


        }else{
          $("error").innerHTML = "请输入正确的密码!";
          $("error").style.display="block";
          setInterval(function(){
            $("error").style.display="none";
          },2500);
        }
    //
      };
      // 密码的隐藏和显示
      $("eye-show").onclick=function(){
        $("error").style.display="none";
        $("eye-hide").style.display="block";
        $("eye-show").style.display="none";
        $("passId").type="password";
      };
      $("eye-hide").onclick=function(){
        $("eye-show").style.display="block";
        $("eye-hide").style.display="none";
        $("passId").type="text";
      };

      $("success").onclick=function(){
        if(regObj.test($("passId").value)){
          $("error").innerHTML = "";
          $("error").style.display="none";

          window.location.href="/#/login";

        }else{
          $("error").innerHTML = "请输入正确的密码!";
          $("error").style.display="block";
          setInterval(function(){
            $("error").style.display="none";
          },2500);
          return;
        }
      }
    }
}
</script>

<style>
  @import '../assets/css/reg.css';
</style>



